<template>
  <div class="ettlementRecords" style="max-width: 100%;">
    <myHeader :isBack="true" title="结算记录"></myHeader>
    <div class="seat-head"></div>
    <div class="main">
      <mt-cell title="批次号：" @click.native="selectBatchNum">
        {{batchNum}}
        <span class="img"></span>
      </mt-cell>
      <mt-cell title="结算日期:" :value="balanceData.bizdate"></mt-cell>
      <mt-cell title="投苗均重/kg:" :value="balanceData.avgWeight"></mt-cell>
      <mt-cell title="上市均重/kg:" :value="balanceData.listedverWeight"></mt-cell>
      <mt-cell title="正品率:" :value="balanceData.realrate"></mt-cell>
      <mt-cell title="料肉比:" :value="balanceData.meatrate"></mt-cell>
    </div>
    <div class="main-bottom">
      <div class="category jus-b ali-c" @click="isShow">
        <div class="left">结算明细:</div>
        <div class="img"></div>
      </div>
      <!-- 结算明细 -->
      <section class="ett-info" v-show="showlist">
        <div class="jus-b ali-c">
          <div class="ett-name">猪苗金额</div>
          <div>{{balanceData.balanceDetail.pigamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">饲料金额</div>
          <div>{{balanceData.balanceDetail.feedsettamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">兽药金额</div>
          <div>{{balanceData.balanceDetail.veterdurgsettamt}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">回收金额</div>
          <div>{{balanceData.balanceDetail.recycleamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">扣罚金额</div>
          <div>{{balanceData.balanceDetail.lrbamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">补贴金额</div>
          <div>{{balanceData.balanceDetail.subsidies}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">结算金额</div>
          <div>{{balanceData.balanceDetail.settamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">补缴保证金</div>
          <div>{{balanceData.balanceDetail.payment}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">扣代付款</div>
          <div>{{balanceData.balanceDetail.payforcustamount}}</div>
        </div>
        <div class="jus-b ali-c">
          <div class="ett-name">应付金额</div>
          <div>{{balanceData.balanceDetail.payableamount}}</div>
        </div>
      </section>
    </div>
  
  
    <!--选择批次-->
    <mt-popup
      v-model="switchBatchNumPopup"
      position="bottom">
      <mt-picker :slots="batchNumSlots"
                 value-key="name"
                 :show-toolbar="true"
                 ref="batchPicker">
        <span @click="handleBatchConfirm" class="sure-btn">确认</span>
      </mt-picker>
    </mt-popup>

  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import {getEttlementBatchListList, getBalanceRecord} from '@/api/finance'
  import { Indicator } from 'mint-ui'

  export default {
    name: 'EttlementRecords',
    data() {
      return {
        switchBatchNumPopup: false,
        batchNumSlots: [{
          values: [{
            id: '',
            name: ''
          }],
          textAlign: 'center'
        }],
        batchNum: '',  //批次号
        batchId: '',
        balanceData: {
          bizdate: '',
          avgWeight: '',
          listedverWeight: '',
          realrate: '',
          meatrate: '',
          balanceDetail: {
            pigamount: '',
            feedsettamount: '',
            veterdurgsettamt: '',
            recycleamount: '',
            lrbamount: '',
            subsidies: '',
            settamount: '',
            payment: '',
            payforcustamount: '',
            payableamount: ''
          }
        },
        showlist: true
      }
    },
    components: {
      myHeader
    },
    created() {
      this._getBatchListList();
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    },
    methods: {
      _getBatchListList() {
        getEttlementBatchListList().then(res => {
          if (res.data && res.data.length) {
            this.batchNumSlots[0].values = res.data.map((item, index) => {
              return {
                name: item.name,
                id: item.id
              };
            });
  
            this.batchId = this.batchNumSlots[0].values[0].id;
            this.batchNum = this.batchNumSlots[0].values[0].name;
            this._getBalanceRecord(this.batchId);
          } else {
            Indicator.close();
          }
        }).catch(err => {
          Indicator.close();
        });
      },
      _getBalanceRecord(batchId) {
        getBalanceRecord(batchId).then(res => {
          Indicator.close();
          let data = res.data;
          this.balanceData = {
              bizdate: data.bizdate,
              avgWeight: data.avgWeight,
              listedverWeight: data.listedverWeight,
              realrate: data.realrate,
              meatrate: data.meatrate,
              balanceDetail: {
                pigamount: data.balanceDetail.pigamount,
                feedsettamount: data.balanceDetail.feedsettamount,
                veterdurgsettamt: data.balanceDetail.veterdurgsettamt,
                recycleamount: data.balanceDetail.recycleamount,
                lrbamount: data.balanceDetail.lrbamount,
                subsidies: data.balanceDetail.subsidies,
                settamount: data.balanceDetail.settamount,
                payment: data.balanceDetail.payment,
                payforcustamount: data.balanceDetail.payforcustamount,
                payableamount: data.balanceDetail.payableamount
            }
          }
        }).catch(err => {
          Indicator.close();
        })
      },
      selectBatchNum() {
        if (this.batchNumSlots[0].values[0].name) {
          this.switchBatchNumPopup = !this.switchBatchNumPopup;
        } else {
          this.$toast({
            message: '无可选批次，请核实业务场景！',
            duration: 1000
          });
        }
      },
      handleBatchConfirm() {
        this.batchNum = this.$refs.batchPicker.getValues()[0].name;
        this.batchId = this.$refs.batchPicker.getValues()[0].id;
        this.batchId ? this._getBalanceRecord(this.batchId) : null;
        this.switchBatchNumPopup = false;
      },
      isShow() {
        this.showlist = !this.showlist;
      }
    },
  }
</script>

<style scoped lang="scss">
  .ettlementRecords {
    min-height: 100vh;
    background-color: #F8F8F8;
  }

  .main {
    margin-top: .12rem;
    .img {
      width: .1rem;
      height: .06rem;
      margin-left: .08rem;
      background: url('~@/assets/icon-next.png') no-repeat;
      background-size: 100%;
    }
  }

  .main-bottom {
    background-color: #fff;
    .category {
      padding: 0 .14rem;
      height: .45rem;
      font-size: .16rem;
      border-bottom: 1px solid #ddd;
      .img {
        width: .12rem;
        height: .08rem;
        background: url('~@/assets/icon-next.png') no-repeat;
        background-size: 100%;
      }
    }
    // 结算明细
    .ett-info {
      padding: .17rem .15rem 0 .3rem;
      .jus-b {
        height: .3rem;
        font-size: .14rem;
        color: rgba(136, 136, 136, 1);
      }
    }
  }

  .mint-popup {
    width: 100%;
    height: 25%;
  
    /deep/ .picker-toolbar {
      height: 0;
    }
  
    .sure-btn {
      position: absolute;
      top: .05rem;
      right: .05rem;
      padding: .05rem .20rem;
      font-size: .16rem;
      z-index: 99;
    }
  }

  .pickers {
    width: 100%;
    background: #FFFFFF;
    position: absolute;
    z-index: 13;
  }

</style>



